<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item v-if="this.istea == 1">教师管理</el-breadcrumb-item>
      <el-breadcrumb-item v-if="this.istea == 0">学生管理</el-breadcrumb-item>
      <el-breadcrumb-item>著作权</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <!-- 搜索 -->
      <el-row :gutter="20">
        <el-col>
          <el-form ref="formRef" :model="queryVo" label-width="90px" inline>
            <el-form-item label="作者">
              <el-input
                v-if="this.istea == 1"
                v-model="queryVo.teaName"
                @keyup.enter.native="queryPage"
                placeholder="请输入作者"
                clearable
                @clear="queryPage"
              ></el-input>

              <el-input
                v-if="this.istea == 0"
                v-model="queryVo.stuName"
                @keyup.enter.native="queryPage"
                placeholder="请输入作者"
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            <el-form-item label="论文题目" prop="paperName">
              <el-input
                v-model="queryVo.paperName"
                @keyup.enter.native="queryPage"
                placeholder="请输入论文题目"
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            <el-form-item label="论文类型" prop="paperType">
              <el-input
                v-model="queryVo.paperType"
                @keyup.enter.native="queryPage"
                placeholder="请输入论文类型"
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>

            <el-form-item v-if="this.istea == 1" label="所属单位" prop="belongDept">
              <el-input
                v-model="queryVo.belongDept"
                @keyup.enter.native="queryPage"
                placeholder="请输入所属单位"
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            <el-form-item v-else label="所属学院" prop="belongDept">
              <el-input
                v-model="queryVo.belongDept"
                @keyup.enter.native="queryPage"
                placeholder="请输入所属学院"
                clearable
                @clear="queryPage"
              ></el-input>
            </el-form-item>
            
            <el-form-item>
              <el-button type="primary" icon="el-icon-search" @click="queryPage"
                >搜索</el-button
              >
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>

      <el-row :gutter="10" class="el-row">
        <el-col :span="1.5">
          <el-button
            type="primary"
            icon="el-icon-plus"
              v-if="is_authority.is_user_add"
            size="mini"
            @click="goto"
            >新增</el-button
          >
          
        </el-col>
        
        <el-col :span="1.5">
          <el-button
            type="success"
            icon="el-icon-edit"
            size="mini"
            :disabled="single"
            @click="goto"
            >修改</el-button
          >
          
        </el-col>
        <el-col :span="1.5">
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            :disabled="multiple"
            v-if="is_authority.is_user_del"
            @click="del"
            >删除</el-button
          >
          
        </el-col>
        <el-col :span="1.5">
          <el-button type="info" icon="el-icon-upload2" size="mini"
            >导入</el-button
          >
          <!-- @click="handleImport" -->
          <!-- v-hasPermi="['system:user:import']" -->
        </el-col>
        <el-col :span="1.5">
          <el-button type="warning" icon="el-icon-download" size="mini"
            >导出</el-button
          >
          <!-- @click="handleExport"
                      v-hasPermi="['system:user:export']" -->
        </el-col>
        <!-- <right-toolbar :showSearch.sync="showSearch" @queryTable="getList"></right-toolbar> -->
      </el-row>

      <!-- 数据 -->
      <el-table
        :data="List"
        border
        stripe
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="50" align="center" />

        <el-table-column
          type="index"
          width="80"
          align="center"
          label="用户编号"
        ></el-table-column>

        <el-table-column
          prop="paperName"
          label="论文题目"
          align="center"
        ></el-table-column>

        <el-table-column
          v-if="this.istea == 1"
          prop="belongDept"
          label="所属单位"
          align="center"
          show-overflow-tooltip
        ></el-table-column>

        <el-table-column
          v-else
          prop="belongDept"
          label="所属学院"
          align="center"
          show-overflow-tooltip
        ></el-table-column>

        <el-table-column
          v-if="this.istea == 1"
          prop="teaName"
          label="所有作者"
          align="center"
        ></el-table-column>

        <el-table-column
          v-if="this.istea == 0"
          prop="stuName"
          label="所有作者"
          align="center"
        ></el-table-column>

        <el-table-column
          prop="subjectCategory"
          label="学科门类"
          align="center"
          show-overflow-tooltip
        ></el-table-column>

        <el-table-column
          prop="paperPublishTime"
          label="发表/出版日期"
          align="center"
          show-overflow-tooltip
        ></el-table-column>

        <el-table-column
          prop="firstlevelDiscipline"
          label="一级学科"
          align="center"
          show-overflow-tooltip
        ></el-table-column>

        <el-table-column
          prop="paperType"
          label="论文类型"
          align="center"
          show-overflow-tooltip
        ></el-table-column>

        <el-table-column
          prop="paperPublication"
          label="发表刊物 /论文集"
          align="center"
          show-overflow-tooltip
        ></el-table-column>

        <el-table-column label="操作" width="180px" align="center">
          <template slot-scope="scope">
            <!-- 修改 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="编辑"
              v-if="is_authority.is_user_upt"
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="goto(scope.row)"
              ></el-button>
            </el-tooltip>
            <!-- 删除 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="删除"  v-if="is_authority.is_user_del"
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="del(scope.row)"
              ></el-button>
            </el-tooltip>
            <!-- 详情 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="详情"
              placement="top-start"
              :enterable="false"
            >
              
              <el-button
                type="warning"
                icon="el-icon-document"
                size="mini"
                @click="gotoDetail(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page.current"
        :page-sizes="[5, 10, 20, 30, 40]"
        :page-size="page.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
      ></el-pagination>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  data() {
    return {
      is_authority: {
        is_user_del: false,
        is_user_upt: false,
        is_user_add: false,
        is_user_add_role: false
      },
      istea : undefined,
      stuOptions: [],
      Form: {},
      ids: [],
      open: false,
      // 非单个禁用
      single: true,
      // 非多个禁用
      multiple: true,
      List: [],
      queryVo: {
        stuName: undefined,
        page: {
          current: 1,
          size: 5
        }
      },
      page: {
        current: 1,
        size: 5,
        total: 0
      }
    }
  },
  created() {
    this.isAuthority()
    this.queryPage()
  },
  watch: {
    $route(to, from) {
        this.queryPage()
    }
  },
  computed: {
    ...mapState(['btus']),
  },
  methods: {
    isAuthority() {
      var arr = this.btus.split(',')
      for (var key in this.is_authority) {
        if (arr.indexOf(key) != -1) {
          this.is_authority[key] = true
        }
      }
    },
    gotoDetail(row){
      const id = row.id || this.ids
      if (this.istea == 1) {
        this.$router.push({ path: '/paperInfoDtl', query: { id: id,istea : this.istea,name : row.teaName} })
      }else{
        this.$router.push({ path: '/paperInfoDtl', query: { id: id,istea : this.istea,name : row.stuName} })
      }
    },
    goto(row) {
      if (row.id) {
        this.$router.push({ path: '/paperInfoUpt', query: { id: row.id, istea : this.istea } })
        
      }else{
        this.$router.push({ path: '/paperInfoUpt', query: {istea : this.istea } })

      }
      
    },

    unique(arr) {
      const res = new Map()
      return arr.filter(arr => !res.has(arr.id) && res.set(arr.id, 1))
    },

    closeInit() {
      // console.log(this.Form);
      this.Form = {}
      this.$refs.formRef.resetFields()
    },

    del(row) {
      if (row.id) {
        var a = row.id.split(',')
      }
      // console.log(row)
      const userid = a || this.ids
      this.$confirm('确认要删除吗?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          const { data: res } = await this.$http.delete(
            '/paperInfo/removeByIdPaperInfos',
            {
              data: userid
            }
          )
          // console.log(res);
          this.queryPage()
          this.List = []
        })
        .then(() => {
          this.$message({
            showClose: true,
            message: '删除成功',
            type: 'success'
          })
        })
    },
    async queryPage() {
      this.istea = this.$route.params.id
      if (this.istea == 1) {
        const { data: res } = await this.$http.post(
          '/paperInfo/queryTeaPaperInfoList',
          this.queryVo
        )
        console.log(res.data.dataList.records)
        if (res.code != 20000) return this.$message.error(res.message)
        this.List = res.data.dataList.records
        this.page.total = res.data.dataList.total
        this.page.current = res.data.dataList.current
        this.page.size = res.data.dataList.size
        // console.log(this.List)
      } else {
        const { data: res } = await this.$http.post(
          '/paperInfo/queryStuPaperInfoList',
          this.queryVo
        )
        console.log(res.data.dataList.records)
        if (res.code != 20000) return this.$message.error(res.message)
        this.List =res.data.dataList.records
        this.page.total = res.data.dataList.total
        this.page.current = res.data.dataList.current
        this.page.size = res.data.dataList.size
        // console.log(this.List)
      }
    },
    //选择框
    handleSelectionChange(selection) {
      this.ids = selection.map(item => item.id)
      this.single = selection.length != 1
      this.multiple = !selection.length
    },
    // 监听每页展示的条数
    handleSizeChange(newSize) {
      this.queryVo.page.size = newSize
      this.queryPage()
    },
    //监听页码
    handleCurrentChange(newCurrent) {
      this.queryVo.page.current = newCurrent
      this.queryPage()
    },
    closeInit() {
      // console.log(this.Form);
      this.Form = {}
      this.$refs.formRef.resetFields()
    }
  }
}
</script>
<style lang="less" scoped>
.el-table {
  margin-top: 15px;
  font-size: 12px;
}
.el-row {
  display: flex;
  align-items: center;
}
.el-pagination {
  margin-top: 15px;
}
</style>
